/**
 * This manager is responsible for storing, requesting and managing polling urls
 * When ad response comes from the sever, we take pollingUrls from adResponse and store them to array called _arrayOfpollingURLs
 * _arrayOfpollingURLs is synchronized with localStorage to save each polling url for 24 hours (expiration time). If pollingUrl expires or
 * server return 'true' for specific pollingUrl we remove it ( and synchronize _arrayOfpollingURLs with localStorage)
 *
 * We request pollingUrls when:
 * * SDK is being initialized
 * * Ad unit ended no matter reason ( error, user closed ad, ad ended naturally etc)
 * * 5 seconds after Ad unit ended
 *
 *  Server returns 'true' for pollingURL only if developer in fact received money for commercial ( for example user watched full video ad or installed proposed application)
 *  then I send information to sender as well as execut callbacs and dispatch events on browser with proper event
 */

import { requestUrl } from './WebManager'
import { localStorageSavedPollingURLkey, senderEvents, debug } from './../constants'
import CommunicationManager from './CommunicationManager'
import DeveloperCallbackManager from './DeveloperCallbackManager'
let _arrayOfpollingURLs

export function registerPollingUrl (adResponse) {
  if (!_arrayOfpollingURLs) {
    _arrayOfpollingURLs = _loadArrayFromLocalStorage()
  }
  adResponse.ads.forEach(item => {
    _addToArray(item)
  })
  _saveArrayToLocalStorage()
}
export function registerSpecificAdItem (adItem) {
  if (!_arrayOfpollingURLs) {
    _arrayOfpollingURLs = _loadArrayFromLocalStorage()
  }
  _addToArray(adItem)
  _saveArrayToLocalStorage()
}

export function requestPollingUrls () {
  if (!_arrayOfpollingURLs) {
    _arrayOfpollingURLs = _loadArrayFromLocalStorage()
  }
  _makeRequestOnEachpollingUrl()
  setTimeout(() => {
    _makeRequestOnEachpollingUrl()
  }, 5000)
}

function _addToArray (item) {
  let date = new Date()
  date.setDate(date.getDate() + 1) // expiration date is 24h
  _arrayOfpollingURLs.push({
    url: item.pollingUrl,
    expirationDate: date
  })
}
function _saveArrayToLocalStorage () {
  if (!window.localStorage) {
    throw new Error('Local storage is not available')
  }
  window.localStorage.setItem(localStorageSavedPollingURLkey, JSON.stringify(_arrayOfpollingURLs))
}
function _loadArrayFromLocalStorage () {
  if (!window.localStorage) {
    throw new Error('Local storage is not available')
  }
  let tmp = JSON.parse(window.localStorage.getItem(localStorageSavedPollingURLkey))
  if (tmp) {
    tmp.forEach(item => {
      item.expirationDate = new Date(item.expirationDate)
    })
    return tmp
  }
  return []
}
function _makeRequestOnEachpollingUrl () {
  if (!_arrayOfpollingURLs) {
    throw new Error('Error during requesting polling urls: variable _arrayOfpollingURLs is not defined')
  }
  let actualDate = new Date()
  _arrayOfpollingURLs.forEach(pollingObject => {
    /**
     * Make sure that pollingURL is still valid
     */
    if (actualDate > pollingObject.expirationDate) {
      _removepollingURL(pollingObject)
      return
    }
    requestUrl(pollingObject.url)
      .then((response) => {
        response = JSON.parse(response)
        // if (debug) {
        //   console.groupCollapsed('polling URL response status:', response.status)
        //   console.log('polling Object', pollingObject)
        //   console.log('Response', response)
        //   console.groupEnd()
        // }
        if (response.status === 'true') {
          _positivepollingURLFeedback({
            pollingObject, response
          })
        }
      })
      .catch((error) => {
        throw new Error('Error during request of polling url ', error)
      })
  })
}
function _positivepollingURLFeedback (params) {
  let { pollingObject, response } = params
  CommunicationManager.send({
    action: senderEvents.POSITIVE_POLLING_URL,
    pollingObject,
    response
  })
  DeveloperCallbackManager.onConversionComplete()
  _removepollingURL(pollingObject)
}
function _removepollingURL (item) {
  if (!_arrayOfpollingURLs) {
    throw new Error('Error during remove of polling url: variable _arrayOfpollingURLs is not defined')
  }
  let index = _arrayOfpollingURLs.indexOf(item)
  if (index > -1) {
    _arrayOfpollingURLs.splice(index, 1)
  }
  _saveArrayToLocalStorage()
}



