import  SessionManager from './SessionManager'
import { debug, senderEvents } from './../constants'
import  CommunicationManager, { cast, castManager }  from './CommunicationManager'
let _instance
let _senderStorage

class SenderManager {
  constructor () {
    _senderStorage = new SenderDeviceStorage()
    this.wasFirstSenderConnected = false
  }

  handleSenderConnected (event) {
    _senderStorage.add(event)
    SessionManager.requestSession()
    if (debug) {
      console.groupCollapsed('%c Sender Connected ', 'color:#FFD700;background-color:#333333;padding:5px 10px;')
      console.log('STORAGE AFTER')
      console.table(_senderStorage.getStorage())
      console.log('EVENT: ', event)
      console.groupEnd()
    }
  }

  broadCastSessionInfo (session) {
    CommunicationManager.send({
      action: senderEvents.NEW_SESSION,
      sessionObject: session
    })
  }

  handleSenderDisconnected (event) {
    _senderStorage.remove({
      id: event.senderId,
      reason: event.reason
    })
    SessionManager.requestSession()
    if (debug) {
      console.groupCollapsed('%c Sender Disconnected ', 'color:#FFD700;background-color:#333333;padding:5px 10px;')
      console.log('STORAGE AFTER')
      console.table(_senderStorage.getStorage())
      console.log('Event: ', event)
      console.groupEnd()
    }
  }

  getSendersInfo () {
    let data = []
    let storageMap = _senderStorage.getStorage()
    for (let key in storageMap) {
      data.push({
        userAgent: storageMap[ key ].userAgent,
        identifierForAds: ''
      })
    }
    return data
  }

  updateSenderInfo (event) {
    // TODO: finish updating sender info
    console.log('Update Sender Info', event)
  }
}

class SenderDeviceStorage {
  constructor () {
    this.senderStorage = {}
  }

  add (info) {
    if (this.senderStorage[ info.senderId ]) {
      throw new Error('Sender Manager: senderId already exists in storage')
    }
    this.senderStorage[ info.senderId ] = {
      userAgent: info.userAgent,
      id: info.senderId,
      data: info.data
    }
  }

  remove (params) {
    if (!this.senderStorage[ params.id ]) {
      throw new Error('This sender does not belong to saved senders!')
    }
    delete this.senderStorage[ params.id ]
  }

  /**
   * Returns DEEP COPY of storage object
   */
  getStorage () {
    return JSON.parse(JSON.stringify(this.senderStorage))
  }
}

function getInstance () {
  if (!_instance) _instance = new SenderManager()
  return _instance
}

export default getInstance()



