import videojsCss from './styles/videojs.css'           // eslint-disable-line
import styles from './styles/main.scss'                 // eslint-disable-line
import lessStyles from './components/timer/timer.less'  // eslint-disable-line
import * as ColorTVSDK from './scripts/ColorTVSDK'
import EventManager from './scripts/Singleton_Managers/EventManager'
import { placements, events } from './scripts/constants'
import './scripts/Other/onStartup'
import vast from 'vast_client'
importVideoLibraries()

const SharedSingleton = (function () {
  let instance

  function createInstance () {
    'use strict'
    instance = {
      loadAd: ColorTVSDK.loadAd,
      showAd: ColorTVSDK.showAd,
      init: ColorTVSDK.init,
      setUserId: ColorTVSDK.setUserId,
      isAdOpened: ColorTVSDK.isAdOpened,
      onSenderConnected: ColorTVSDK.onSenderConnected,          // DEV MUST CALL THIS IN HIS APP
      onSenderDisconnected: ColorTVSDK.onSenderDisconnected,    // DEV MUST CALL THIS IN HIS APP
      onSystemVolumeChanged: ColorTVSDK.onSystemVolumeChanged,  // DEV MUST CALL THIS IN HIS APP
      onShutdown: ColorTVSDK.onShutdown,                        // DEV MUST CALL THIS IN HIS APP
      onAdLoaded: undefined,              // DEV CAN PASS HERE HIS FUNCTION
      onAdNotLoaded: undefined,           // DEV CAN PASS HERE HIS FUNCTION
      onAdExpired: undefined,             // DEV CAN PASS HERE HIS FUNCTION
      onSdkInitialized: undefined,        // DEV CAN PASS HERE HIS FUNCTION
      onUnsupportedAdType: undefined,     // DEV CAN PASS HERE HIS FUNCTION
      onConversionComplete: undefined     // DEV CAN PASS HERE HIS FUNCTION
    }
    ColorTVSDK.SDKInstance = instance
    return instance
  }

  return {
    Placements: placements,
    Events: events,
    EventManager: EventManager,
    getInstance: function () {
      return instance || createInstance()
    }
  }
})()

function importVideoLibraries () {
  'use strict'
  System.import('video.js').then((videojs) => {
    window.videojs = videojs
    window.videojs.Html5 = videojs.getTech('Html5')
    window.videojs.Flash = videojs.getTech('Flash')
    System.import('videojs_ads').then(() => {
      window.vast = window.DMVAST = vast
      System.import('videojs_vast').then(() => {
      })
    })
  })
}

window ? window.ColorTVSDK = SharedSingleton : false
export default SharedSingleton



