import NavigationManager from '../Singleton_Managers/NavigationManager'
import { convertToSVG, computeRelativePosition } from '../Other/helpers'
import CommunicationManager from '../Singleton_Managers/CommunicationManager'
import { adTypes, senderEvents, cssStartIndex } from './../constants'
import { requestUrl } from '../Singleton_Managers/WebManager'
import { closeAd, setCurrentlyDisplayed } from '../ColorTVSDK'
import { config } from '../Singleton_Managers/ConfigManager'
let X = require('./../../img/svg/x.svg')
let _interstitial = require('html!./../../html/interstitial.html')
let _adResponse, _ad, _shakeActionInterval, _navigationManager, _selectedImage, _htmlTemplate, _inactivityInterval
export let slider

// https://colortv.wake.io/set/66/519
export function showInterstitialScreen (params) {
  'use strict'
  let { node, adResponse, dontRunAdStarted } = params
  _htmlTemplate = document.createElement('div')
  _htmlTemplate.innerHTML = _interstitial
  _adResponse = adResponse
  _ad = adResponse.ads[ 0 ]
  setUpCarousel()
  placeAd(_ad)
  node.innerHTML = _htmlTemplate.innerHTML
  setCurrentlyDisplayed(adTypes.INTERSTITIAL)
  if (!dontRunAdStarted) {
    CommunicationManager.send({
      action: senderEvents.AD_STARTED,
      advertisement: _adResponse,
      config: config
    })
  }
  setUpButtons(_ad.actionButton)
  setUpNavigationManager()
  requestUrl(_ad.impressionUrl)
}
function setUpCarousel () {
  'use strict'
  _selectedImage = cssStartIndex
  _htmlTemplate.querySelector('#screenshotElement').src = _ad.screenshots[ _selectedImage - 1 ]
  for (let i = 0; i < _ad.screenshots.length; i++) {
    let dot = document.createElement('div')
    dot.classList.add('dot')
    _htmlTemplate.querySelector('.dots').appendChild(dot)
  }
  _htmlTemplate.querySelector(`.dots .dot:nth-child(${_selectedImage})`).classList.add('active')
}
function setInactivityInterval () {
  'use strict'
  _inactivityInterval = setInterval(() => {
    moveSlider('right')
  }, 7000)
}
function sliderNavigation (direction) {
  'use strict'
  clearInactivityInterval()
  setInactivityInterval()
  if (document.getElementById('slider').classList.contains('focused')) {
    moveSlider(direction)
  }

  if (document.getElementById('subscribeButton').classList.contains('focused') && _shakeActionInterval) {
    clearShakingInterval()
  } else if (!document.getElementById('subscribeButton').classList.contains('focused') && !_shakeActionInterval) {
    _shakeActionInterval = setInterval(() => {
      shakeSubscribe()
    }, 5000)
  }
}
function moveSlider (direction) {
  'use strict'
  document.querySelector(`.dots .dot:nth-child(${_selectedImage})`).classList.remove('active')
  switch (direction) {
    case 'left':
      _selectedImage -= 1
      if (_selectedImage < 1) {
        _selectedImage = _ad.screenshots.length
      }
      break
    case 'right':
      _selectedImage += 1
      _selectedImage % (_ad.screenshots.length + 1) === 0 ? _selectedImage = 1 : false
      break
  }
  document.querySelector('#screenshotElement').src = _ad.screenshots[ _selectedImage - 1 ]
  document.querySelector(`.dots .dot:nth-child(${_selectedImage})`).classList.add('active')
}
/**
 *  Place small colorTV logo in HTML. Logo is coded in Base64
 */
function setupSmallLogo () {
  'use strict'
  document.querySelector('.interstitial .ColortvSubLogo').innerHTML = '<img src="https://s3.amazonaws.com/assets.replayt.com/chromecast/colortv-mono.png">'
}

function placeAd (ad) {
  placeButtons(ad.actionButton)
  _htmlTemplate.querySelector('.info .appIcon').innerHTML = `<img src="${ad.backgroundUrl}">`
  _htmlTemplate.querySelector('.info .descriptionBox .headerHolder .headerText .header').innerHTML = ad.title
  _htmlTemplate.querySelector('.info .descriptionBox .headerHolder .headerText .ratingHolder .stars').innerHTML =
    (() => {
      let result = ''
      'use strict'
      ad.rating = 4.5
      for (let i = 0; i < Math.floor(ad.rating); i++) {
        result += '<span class="star">&#9733;</span> '
      }
      // Check if there ad.rating is float
      if (ad.rating % 1 > 0) {
        result += '<span class="star half-star">&#9733;</span>'
      }
      return result
    })()
  _htmlTemplate.querySelector('.info .descriptionBox .headerHolder .headerText .ratingHolder .rating').innerHTML = `${ad.reviews} Ratings`
  _htmlTemplate.querySelector('.info .descriptionBox .headerHolder .price .value').innerHTML = ad.price
  _htmlTemplate.querySelector('.info .descriptionBox .headerHolder .price .sideText').innerHTML = 'Download & Run'
  _htmlTemplate.querySelector('.info .descriptionBox .description .value').innerHTML = ad.description
}

function truncateText (classes) {
  'use strict'
  try {
    var el = document.getElementById('description')
    var wordArray = el.innerHTML.split(' ')
    while (el.scrollHeight > el.offsetHeight) {
      wordArray.pop()
      el.innerHTML = wordArray.join(' ') + '...'
    }
  } catch (e) {
    setTimeout(() => {
      truncateText(classes)
    }, 100)
  }
}
function placeButtons (actionButton) {
  'use strict'
  _htmlTemplate.querySelector('#closeButton').innerHTML = convertToSVG(X)
  let subscribe = _htmlTemplate.querySelector('#subscribeButton')
  subscribe.innerHTML = actionButton.text
  subscribe.style.backgroundColor = `rgb(${actionButton.backgroundRed},${actionButton.backgroundGreen},${actionButton.backgroundBlue})`
  subscribe.style.color = `rgb(${actionButton.textRed},${actionButton.textGreen},${actionButton.textBlue})`
}

function setUpNavigationManager () {
  return new Promise(() => {
    _navigationManager = new NavigationManager(document.getElementById('slider'), adTypes.INTERSTITIAL, subscribe)
    CommunicationManager.setNavigationManager(_navigationManager)
    _navigationManager.registerListener(sliderNavigation)
  })
}

function setUpButtons () {
  document.getElementById('subscribeButton').addEventListener('click', () => {
    subscribe()
  }, false)
  document.getElementById('closeButton').addEventListener('click', () => {
    closeInterstitial()
    closeAd()
  }, false)
}
function subscribe () {
  requestUrl(_ad.clickTracker)
  CommunicationManager.send({
    action: senderEvents.ACTION_INVOKED,
    advertisement: _ad
  }, CommunicationManager.lastEventFromSender.senderId)
}
export function closeInterstitial () {
  'use strict'
  clearShakingInterval()
  clearInactivityInterval()
  _navigationManager.removeListener(sliderNavigation)
}

export function clearShakingInterval () {
  'use strict'
  clearInterval(_shakeActionInterval)
  _shakeActionInterval = undefined
}
export function clearInactivityInterval () {
  'use strict'
  clearInterval(_inactivityInterval)
  _inactivityInterval = undefined
}
function shakeSubscribe () {
  'use strict'
  document.getElementById('subscribeButton').classList.add('ctv-shake')
  setTimeout(() => {
    document.getElementById('subscribeButton').classList.remove('ctv-shake')
  }, 1100)
}




