import { buttonCode, channelNamespace, senderEvents, debug, errorMessages } from './../constants'
import { isAdOpened, openedAdResponse, onBackButton } from '../ColorTVSDK'
import SenderManager from './SenderManager'
let _channelConnection, _navigationManager, _instance
export let cast, castManager

class CommunicationManager {
  constructor () {
    cast = window.cast
    this.lastEventFromSender = void 0
    if (!cast) {
      throw new Error('Cast Receiver is missing!')
    }
    if (debug) {
      cast.receiver.logger.setLevelValue('DEBUG')
      this.setupKeyboardNavigation()
    }
    castManager = cast.receiver.CastReceiverManager.getInstance()
    _channelConnection = castManager.getCastMessageBus(channelNamespace)
    _channelConnection.onMessage = this._onMessage.bind(this)
  }

  setNavigationManager (navigationManager) {
    _navigationManager = navigationManager
  }

  removeNavigationManager () {
    _navigationManager = undefined
  }

  getNavigationManager () {
    return _navigationManager
  }

  setupKeyboardNavigation () {
    window.addEventListener('keydown', (charCode) => {
      if (!isAdOpened() || !_navigationManager) {
        return
      }
      switch (charCode.keyCode) {
        case 38:
          _navigationManager.up()
          break
        case 40:
          _navigationManager.down()
          break
        case 37:
          _navigationManager.left()
          break
        case 39:
          _navigationManager.right()
          break
        case 32:
          _navigationManager.center()
          break
      }
    })
  }

  _onMessage (event) {
    let message
    this.lastEventFromSender = event
    try {
      message = JSON.parse(event.data)
    } catch (error) {
      throw new Error('Error during parsing message from sender: ' + error.message)
    }
    console.groupCollapsed('Message from sender')
    console.info(event)
    console.groupEnd()
    if (message.state === buttonCode.CHANNEL_OPENED) {
      this._synchronizeControllerSDK(event)
      return
    }
    if (message.errorCode) {
      // TODO handle errors
      return
    }
    if (_navigationManager && isAdOpened()) {
      switch (message.action) {
        case buttonCode.UP:
          _navigationManager.up(event)
          break
        case buttonCode.DOWN:
          _navigationManager.down(event)
          break
        case buttonCode.CENTER:
          _navigationManager.center(event)
          break
        case buttonCode.LEFT:
          _navigationManager.left(event)
          break
        case buttonCode.RIGHT:
          _navigationManager.right(event)
          break
        case buttonCode.OTHER_ACTION:
          _navigationManager.otherAction(event)
          break
        case buttonCode.ACTION:
          _navigationManager.subscribe(event)
          break
        case buttonCode.BACK:
          onBackButton()
          break
        case buttonCode.RETURNED_TO_APP:
          onBackButton()
          break
        case buttonCode.UPDATE_SENDER_INFO:
          SenderManager.updateSenderInfo(event)
          break
        default:
          throw new Error(`Communication Error: Unrecognized message from sender: "${message.type}"`)
      }
    }
  }

  _synchronizeControllerSDK (event) {
    let senderId = event ? event.senderId : undefined
    if (isAdOpened()) {
      this.send({
        state: senderEvents.AD_STARTED,
        advertisement: openedAdResponse
      }, senderId)
    } else {
      this.send({
        state: senderEvents.AD_CLOSED
      }, senderId)
    }
  }

  onSenderConnected (event) {
    if (!event) {
      console.error(errorMessages[ 16 ])
      return
    }
    SenderManager.handleSenderConnected(event)
    this._synchronizeControllerSDK()
  }

  onSenderDisconnected (event) {
    if (!event) {
      console.error(errorMessages[ 17 ])
      return
    }
    SenderManager.handleSenderDisconnected(event)
  }

  onShutdown () {
  }

  onStandbyChanged () {
  }

  onSystemVolumeChanged () {
  }

  send (msg, senderId) {
    if (_channelConnection && castManager.getSenders().length > 0) {
      if (senderId) {
        console.groupCollapsed('Sender Communication')
        console.log(`Sending on ${channelNamespace} to ${senderId}`)
        console.log('Message: ', msg)
        console.groupEnd()
        _channelConnection.send(senderId, JSON.stringify(msg))
      } else {
        console.groupCollapsed('Sender Communication')
        console.log(`Broadcasting on ${channelNamespace}`)
        console.log('Message: ', msg)
        console.groupEnd()
        _channelConnection.broadcast(JSON.stringify(msg))
      }
    }
  }
}

function getInstance () {
  'use strict';
  if (!_instance) {
    _instance = new CommunicationManager()
  }
  return _instance
}
export default getInstance()



