import { cast } from '../Singleton_Managers/CommunicationManager'
import { sdkVersion } from './../constants'
export function fancySDKoutput () {
  'use strict'
  var args = [
    `%c %c %c ColorTV  | colortv.com | Chromecast SDK ${sdkVersion} | Cast SDK ${cast.receiver.VERSION} %c %c %c`,
    'background: #4fb4fc',
    'background: rgba(4,146,247,.75)',
    'color: #ffffff; background: #0492f7;',
    'background: rgba(4,146,247,.75)',
    'background: #4fb4fc',
    'background: #ffffff'
  ]
  console.log.apply(console, args)
}



