import { actionButtonTypes } from './../constants'
let Mail = require('./../../img/svg/Mail.svg')
let Phone = require('./../../img/svg/Phone.svg')
let SMS = require('./../../img/svg/SMS.svg')
/**
 * Compute top and left value of element depending of actual screen size. Normally in response
 * we receive position on 1920x1080 screen but we have different screen size.
 * @param x
 * @param y
 * @returns {{top: number, left: number}}
 */
export function computeRelativePosition (x, y) {
  'use strict'
  return {
    top: y * (window.innerHeight / 1080),
    left: x * (window.innerWidth / 1920)
  }
}

export function convertToSVG (file) {
  'use strict'
  file = file.replace(/%3C/gi, '<').replace(/%3E/gi, '>').replace('data:image/svg+xml;charset=utf8,', '')
  file = file.substring(1, file.length - 1)
  return file
}

export function hashString (str) {
  let hash = 0
  let char
  if (str.length === 0) return hash
  for (let i = 0; i < str.length; i++) {
    char = str.charCodeAt(i)
    hash = ((hash << 5) - hash) + char
    hash = hash & hash // Convert to 32bit integer
  }
  return hash
}
export function isFunction (func) {
  return (func && typeof func === 'function')
}
/**
 * Ad type may have action type with text on it like call now or mail us now. This function returns proper icon for button.
 * @param actionType Action type from adResponse
 */
export function chooseIconToAdUnitActionButtonType (actionType) {
  switch (actionType) {
    case actionButtonTypes.PHONE:
      return Phone
    case actionButtonTypes.SMS:
      return SMS
    case actionButtonTypes.EMAIL:
      return Mail
    default:
      return Mail
  }
}


