import NavigationManager from '../Singleton_Managers/NavigationManager'
import { convertToSVG } from '../Other/helpers'
import { setCurrentlyDisplayed, onBackButton } from '../ColorTVSDK'
import CommunicationManager from '../Singleton_Managers/CommunicationManager'
import { adTypes, senderEvents } from './../constants'
import { config } from '../Singleton_Managers/ConfigManager'
import * as PollingUrlManager from './../Singleton_Managers/PollingUrlManager'
let X = require('./../../img/svg/x.svg')
import { requestUrl } from '../Singleton_Managers/WebManager'
let _content = require('html!./../../html/content.html')
let _adResponse, tape, _navigationManager, featuredAd

export function showContentScreen (params) {
  let { node, adResponse } = params
  'use strict'
  _adResponse = adResponse
  node.innerHTML = _content
  document.getElementById('closeButton').innerHTML = convertToSVG(X)
  setCurrentlyDisplayed(adTypes.CONTENT_RECOMMENDATION)
  tape = new Tape()
  setUpFeaturedAd()
  setUpNavigation()
  CommunicationManager.send({
    action: senderEvents.AD_STARTED,
    advertisement: _adResponse,
    config: config
  })
  parseAdResponse(_adResponse)
}
function parseAdResponse (adResp) {
  'use strict'
  adResp.ads.forEach((item) => {
    let html = `
            <div class="item">
            <div class="imageHolder">
            <div class="image" style="background-image: url(${item.backgroundUrl})"></div> 
            </div>
            <div class="bodyHolder">
                <div class="playButton"></div>
               <div class="textHolder">
                <div class="headerHolder">
                    <div class="category">${item.category}</div>
                </div>
                <div class="descriptionHolder">
                ${item.title}
                </div>
                </div>
            </div>
          </div>
        `
    tape.addItem(item, html)
  })
}
function setUpFeaturedAd () {
  'use strict'
  featuredAd = document.querySelector('.content #featuredAd')
  var div = document.createElement('div')
  div.classList.add('sidebar')
  div.innerHTML = `
        <div class="item sponsored">
            <div class="imageHolder">
            <div class="image" style="background-image: url(${_adResponse.featuredAd.ads[ 0 ].backgroundUrl})"></div>
            </div>
            <div class="bodyHolder">
                <div class="playButton"></div>
               <div class="textHolder">
                <div class="headerHolder">
                    <div class="category">${_adResponse.featuredAd.ads[ 0 ].category}</div>
                </div>
                <div class="descriptionHolder">
              ${_adResponse.featuredAd.ads[ 0 ].title}
                </div>
                </div>
            </div>
          </div>
    `
  featuredAd.appendChild(div)
  featuredAd.onclick = doAction
}

function setUpNavigation () {
  'use strict'
  _navigationManager = new NavigationManager(document.querySelector('.content #featuredAd'), adTypes.CONTENT_RECOMMENDATION, doAction)
  CommunicationManager.setNavigationManager(_navigationManager)
  _navigationManager.registerListener(tape.navigationHandler())
  setupButtons()
}
function setupButtons () {
  'use strict'
  document.getElementById('closeButton').addEventListener('click', function () {
    onBackButton()
  })
}

function doAction () {
  'use strict'
  let clickedAd, clickTrackerUrl, adObject
  if (_navigationManager.getCurrentElement().id === 'closeButton') {
    return
  }

  if (_navigationManager.getCurrentElement().id === 'featuredAd') {
    clickedAd = _adResponse.featuredAd.ads[ 0 ]
    adObject = clickedAd
    clickTrackerUrl = _adResponse.featuredAd.ads[ 0 ].clickTracker
  } else if (tape && tape.navigation.selected) {
    clickedAd = tape.navigation.selected
    adObject = clickedAd.ad
    clickTrackerUrl = tape.navigation.selected.ad.clickTracker
  }
  PollingUrlManager.registerSpecificAdItem(adObject)
  requestUrl(clickTrackerUrl)
  CommunicationManager.send({
    action: senderEvents.ACTION_INVOKED,
    advertisement: adObject
  }, CommunicationManager.lastEventFromSender.senderId)
  onBackButton()
}

function requestMoreAds () {
  return new Promise((resolve, reject) => {
    if (_adResponse.moreAdsUrl) {
      requestUrl(_adResponse.moreAdsUrl).then((data) => {
        try {
          data = JSON.parse(data)
        } catch (e) {
          throw new Error('Error during parsing response from server : ', e.message)
        }
        if (data.errorCode === 1) {
          resolve(false)
        } else {
          CommunicationManager.send({
            action: senderEvents.MORE_ADS_LOADED,
            advertisement: data
          })
          data.ads.forEach((item) => {
            _adResponse.ads.push(item)
            _adResponse.moreAdsUrl = data.moreAdsUrl
          })
          parseAdResponse(data)
          resolve(true)
        }
      })
    } else {
      console.log('No more ads url present in response')
    }
  })
}

class Tape {
  constructor () {
    this.references = []
    this.lastColumn = new Column(this.references)
    this.numberOfItems = 0
    this.instance = document.querySelector('.content #tape')
    this.position = 0
    this.widthOfViewWindow = document.querySelector('.content .view').clientWidth
    this.reachedAppsLimit = false
    this.navigation = {
      view: {},
      selected: undefined
    }
  }

  addItem (item, html, classes = []) {
    let div = document.createElement('div')
    div.className += 'itemHolder'
    classes.forEach((cls) => {
      div.className += cls
    })
    div.innerHTML = html
    if (!this.lastColumn.spaceLeft > 0) {
      this.lastColumn = new Column(this.references)
    }
    this.lastColumn.add(item, div)
    this.width = this.instance.clientWidth
    this.maxRight = this.width - this.widthOfViewWindow + 40
    if (!this.navigation.view.left) {
      this.navigation.view.left = {
        reference: this.references[ 0 ],
        x: 0
      }
    }
    if (this.navigation.view.left && !this.navigation.view.right && this.references[ 1 ]) {
      this.navigation.view.right = {
        reference: this.references[ 1 ],
        x: 1
      }
    }
  }

  getElement (x, y) {
    if (x >= 0 && x <= this.references.length - 1 && (y === 0 || y === 1)) {
      return this.references[ x ][ y ]
    } else {
      return false
    }
  }

  selectItem (x, y) {
    if (this.navigation.selected) {
      this.downplaySelected(this.navigation.selected.node)
    }

    this.navigation.selected = this.getElement(x, y)
    this.highlightSelected(this.navigation.selected.node)
  }

  highlightSelected (node) {
    if (node) {
      node.classList.add('selected')
      return true
    }
  }

  downplaySelected (node) {
    if (node) {
      node.classList.remove('selected')
      return true
    }
  }

  navigationHandler () {
    let self = this
    let firstTimeWhenNavigationOnFocused = true
    return function (direction) {
      if (!self.instance.classList.contains('focused')) {
        self.navigation.focused = false
        if (self.navigation.selected) {
          self.downplaySelected(self.navigation.selected.node)
        }
        return
      }
      self.navigation.focused = true
      if (firstTimeWhenNavigationOnFocused) {
        firstTimeWhenNavigationOnFocused = false
        if (direction === 'right') {
          self.selectItem(0, 0)
        } else if (direction === 'down') {
          self.selectItem(self.navigation.view.right.x, 0)
        }
        if (self.navigation.selected) {
          self.highlightSelected(self.navigation.selected.node)
        }

        return
      }
      switch (direction) {
        case 'left':
          let onLeft = self.getElementOnLeft(self.navigation.selected.x, self.navigation.selected.y)
          if (onLeft) {
            if (self.isInView(onLeft.x, onLeft.y)) {
              self.selectItem(onLeft.x, onLeft.y)
            } else {
              self.moveView('left')
              self.move('left')
              self.selectItem(onLeft.x, onLeft.y)
            }
          } else {
            firstTimeWhenNavigationOnFocused = true
            self.selectFeatureAd()
          }

          break
        case 'right':
          let onRight = self.getElementOnRight(self.navigation.selected.x, self.navigation.selected.y)
          if (!onRight) {
            return
          }
          if (self.isInView(onRight.x, onRight.y)) {
            self.selectItem(onRight.x, onRight.y)
          } else {
            self.moveView('right')
            self.move('right')
            self.selectItem(onRight.x, onRight.y)
          }
          if (self.isLastColumn(onRight.x) && !self.reachedAppsLimit) {
            requestMoreAds().then((isMoreToLoad) => {
              self.reachedAppsLimit = !isMoreToLoad
            })
          }
          break
        case 'up':
          let above = self.getElementAbove(self.navigation.selected.x, self.navigation.selected.y)
          if (above) {
            if (self.isInView(above.x, above.y)) {
              self.selectItem(above.x, above.y)
            }
          } else {
            firstTimeWhenNavigationOnFocused = true
            self.selectCloseButton()
          }

          break
        case 'down':
          let under = self.getElementUnder(self.navigation.selected.x, self.navigation.selected.y)
          if (self.isInView(under.x, under.y) && under) {
            self.selectItem(under.x, under.y)
          }
          break
        case 'center':
          doAction()
          break
      }
    }
  }

  isLastColumn (x) {
    return this.references.length - 1 === x
  }

  getElementOnRight (x, y) {
    let tmp = this.getElement(x + 1, y)
    if (tmp) {
      return tmp
    } else {
      return false
    }
  }

  getElementOnLeft (x, y) {
    let tmp = this.getElement(x - 1, y)
    if (tmp) {
      return tmp
    } else {
      return false
    }
  }

  getElementAbove (x, y) {
    let tmp = this.getElement(x, y - 1)
    if (tmp) {
      return tmp
    } else {
      return false
    }
  }

  getElementUnder (x, y) {
    let tmp = this.getElement(x, y + 1)
    if (tmp) {
      return tmp
    } else {
      return false
    }
  }

  isInView (x, y) {
    if (this.navigation.view.left.reference.indexOf(this.getElement(x, y)) > -1 || this.navigation.view.right.reference.indexOf(this.getElement(x, y)) > -1) {
      return true
    } else {
      return false
    }
  }

  selectFeatureAd () {
    _navigationManager.performManualChange(document.querySelector('#featuredAd'), 'left')
  }

  selectCloseButton () {
    _navigationManager.performManualChange(document.querySelector('#closeButton'), 'top')
  }

  moveView (direction) {
    switch (direction) {
      case 'left':
        if (this.references[ this.navigation.view.right.x - 1 ]) {
          this.navigation.view.right = {
            reference: this.references[ this.navigation.view.left.x ],
            x: this.navigation.view.left.x
          }
          this.navigation.view.left = {
            reference: this.references[ this.navigation.view.right.x - 1 ],
            x: this.navigation.view.right.x - 1
          }
        }
        break
      case 'right':
        if (this.references[ this.navigation.view.right.x + 1 ]) {
          this.navigation.view.left = {
            reference: this.references[ this.navigation.view.right.x ],
            x: this.navigation.view.right.x
          }
          this.navigation.view.right = {
            reference: this.references[ this.navigation.view.right.x + 1 ],
            x: this.navigation.view.right.x + 1
          }
        }
        break
    }
  }

  move (direction) {
    let unit = 360
    console.log(`DEBUG 
       position:                      ${this.position},
       width of view window:          ${this.widthOfViewWindow},
       width:                         ${this.width}}
       max width:                     ${this.maxRight}`)
    if (direction === 'left') {
      if (this.position !== 0) {
        this.position += unit
        this.position > 0 ? this.position = 0 : false
      }
    } else {
      if (this.position === 0) {
        unit = 255
      }
      this.position -= unit
      if (-this.position > this.maxRight) {
        this.position = -this.maxRight
      }
      if (this.width < this.widthOfViewWindow) {
        this.position = 0
      }
    }
    this.instance.style.transform = `translateX(${this.position}px)`
  }

}
class Column {
  constructor (references) {
    this.spaceLeft = 2
    this.reference = []
    this.x = references.length
    references.push(this.reference)
    let col = document.createElement('div')
    col.className += 'column'
    document.querySelector('.content .tape').appendChild(col)
    this.htmlHandler = col
  }

  add (item, node) {
    if (this.spaceLeft > 0) {
      this.htmlHandler.appendChild(node)
      this.spaceLeft--
      let tmp = {
        node: node,
        ad: item,
        x: this.x,
        y: this.reference.length
      }
      this.reference.push(tmp)
      return tmp
    } else {
      return false
    }
  }
}



