import NavigationManager from '../Singleton_Managers/NavigationManager'
import { computeRelativePosition, convertToSVG, chooseIconToAdUnitActionButtonType } from '../Other/helpers'
import { closeAd, setCurrentlyDisplayed } from '../ColorTVSDK'
import { adTypes, senderEvents } from './../constants'
import { requestUrl } from '../Singleton_Managers/WebManager'
import { config } from '../Singleton_Managers/ConfigManager'
import CommunicationManager from '../Singleton_Managers/CommunicationManager'
let X = require('./../../img/svg/x.svg')
let _engagement = require('html!./../../html/engagement.html')
let _adResponse, _ad

export function showEngagementScreen (params) {
  'use strict'
  let { node, adResponse, dontRunAdStarted } = params
  _adResponse = adResponse
  _ad = adResponse.ads[ 0 ]
  node.innerHTML = _engagement
  setCurrentlyDisplayed(adTypes.ENGAGEMENT)
  if (!dontRunAdStarted) {
    CommunicationManager.send({
      action: senderEvents.AD_STARTED,
      advertisement: _adResponse,
      config: config
    })
  }
  placeAd(_ad)
  setUpNavigationManager()
  setUpButtons()
  requestUrl(_ad.impressionUrl)
}

function placeAd (ad) {
  let submitButtonPosition = computeRelativePosition(ad.actionButton.xPosition, ad.actionButton.yPosition)
  document.querySelector('#colorTvOverlay .engagement').style.backgroundImage = `url(${ad.backgroundUrl})`
  document.querySelector('#colorTvOverlay .engagement .closeButton').innerHTML = convertToSVG(X)
  let actionButton = document.querySelector('#colorTvOverlay  .engagement .actionButton')
  actionButton.innerHTML = `
            ${convertToSVG(chooseIconToAdUnitActionButtonType(_adResponse.ads[ 0 ].actionButton.actionType))}
            ${ad.actionButton.text}
    `
  actionButton.style.left = `${submitButtonPosition.left}px`
  actionButton.style.top = `${submitButtonPosition.top}px`
  actionButton.style.backgroundColor = `rgb(${ad.actionButton.backgroundRed},${ad.actionButton.backgroundGreen},${ad.actionButton.backgroundBlue})`
  actionButton.style.color = `rgb(${ad.actionButton.textRed},${ad.actionButton.textGreen},${ad.actionButton.textBlue})`
}

function setUpNavigationManager () {
  let navigationManager = new NavigationManager(document.getElementById('subscribeButton'), adTypes.ENGAGEMENT, subscribe)
  CommunicationManager.setNavigationManager(navigationManager)
}

function setUpButtons () {
  if (_adResponse.showCastControls && _adResponse.showCastControls === 'true') {
    document.querySelector('#subscribeButton').addEventListener('click', () => {
      subscribe()
    })
    document.querySelector('#closeButton').addEventListener('click', () => {
      closeAd()
    })
  } else {
    document.querySelector('#subscribeButton').remove()
    document.querySelector('#closeButton').remove()
  }
}

function subscribe () {
  requestUrl(_ad.clickTracker)
  CommunicationManager.send({
    action: senderEvents.ACTION_INVOKED,
    advertisement: _ad
  }, CommunicationManager.lastEventFromSender.senderId)
}



