import { SDKInstance } from '../ColorTVSDK'
import EventManager from './EventManager'
import { events } from './../constants'
import { isFunction } from '../Other/helpers'
let _instance

class DeveloperCallbackManager {

  onAdLoaded () {
    EventManager.dispatchEvent(events.AD_LOADED)
    if (isFunction(SDKInstance.onAdLoaded)) {
      SDKInstance.onAdLoaded()
    }
  }

  onAdClosed () {
    EventManager.dispatchEvent(events.AD_CLOSED)
    if (isFunction(SDKInstance.onAdClosed)) {
      SDKInstance.onAdClosed()
    }
  }

  onAdNotLoaded () {
    EventManager.dispatchEvent(events.AD_NOT_LOADED)
    if (isFunction(SDKInstance.onAdNotLoaded)) {
      SDKInstance.onAdNotLoaded()
    }
  }

  onAdExpired (placement) {
    EventManager.dispatchEvent(events.AD_EXPIRED)
    if (isFunction(SDKInstance.onAdExpired)) {
      SDKInstance.onAdExpired(placement)
    }
  }

  onSdkInitialized () {
    EventManager.dispatchEvent(events.SDK_INITIALIZED)
    if (isFunction(SDKInstance.onSdkInitialized)) {
      SDKInstance.onSdkInitialized()
    }
  }

  onUnsupportedAdType () {
    EventManager.dispatchEvent(events.UNSUPPORTED_AD_TYPE)
    if (isFunction(SDKInstance.onUnsupportedAdType)) {
      SDKInstance.onUnsupportedAdType()
    }
  }

  onConversionComplete () {
    EventManager.dispatchEvent(events.CONVERSION_COMPLETE)
    if (isFunction(SDKInstance.onConversionComplete)) {
      SDKInstance.onConversionComplete()
    }
  }
}

function getInstance () {
  'use strict'
  if (!_instance) {
    _instance = new DeveloperCallbackManager()
  }
  return _instance
}

export default getInstance()



