/* global videojs */
import Timer from './../../components/timer/timer'
import { closeAd, setCurrentlyDisplayed } from '../ColorTVSDK'
import NavigationManager from '../Singleton_Managers/NavigationManager'
import CommunicationManager  from '../Singleton_Managers/CommunicationManager'
import { requestUrl } from '../Singleton_Managers/WebManager'
import { senderEvents, adTypes } from './../constants'
import { config } from '../Singleton_Managers/ConfigManager'
import { isFunction } from '../Other/helpers'
let _videoHTML = require('html!./../../html/video.html')
let _onAdEnd, _ad, _player, _timer

export function showVideo (node, adResponse, onAdEnd) {
  'use strict'
  setCurrentlyDisplayed(adTypes.VIDEO)
  _onAdEnd = onAdEnd
  _ad = adResponse.ads[ 0 ]
  node.innerHTML = _videoHTML
  setUpNavigationManager()
  CommunicationManager.send({
    action: senderEvents.AD_STARTED,
    advertisement: adResponse,
    config: config
  })
  videojs('vid1', {
    plugins: {
      ads: {},
      vast: {
        url: adResponse.ads[ 0 ].markupUrl
      }
    }
  }).ready(function () {
    _player = this
    let tech = _player.tech({ IWillNotUseThisInPlugins: true })
    let div = document.createElement('div')
    div.classList.add('colorTvloaderHolder')
    div.innerHTML = '<img src="https://s3.amazonaws.com/assets.replayt.com/chromecast/loader.gif" class="colorTVLoader .vjs-loading-spinner"/>'
    document.getElementById('vid1').appendChild(div)
    document.querySelector('#colorTvOverlay .content .video').insertBefore(Timer.getHtml(), document.querySelector('#colorTvOverlay .content .video').firstChild)
    _player.one('readyforpreroll', () => {
      _player.on(tech, 'loadedmetadata', () => {
        document.querySelector('.colorTvloaderHolder').remove()
        _timer = new Timer(document.querySelector('#colorTvOverlay .content .video .radial-progress'), _player.duration())
        _synchronizeTimerAndVideo()
      })
      _player.one('adended', function () {
        closeVideo(true)
      })
    })
    _player.play()
  })
}
/**
 * Takes video current play position and set it in timer
 * @private
 */
function _synchronizeTimerAndVideo () {
  'use strict'
  let seconds, interval
  let calcTimer = () => {
    if (!_player) {
      clearInterval(interval)
      return
    }
    seconds = _player.duration().toFixed(0) - Math.ceil(_player.currentTime())
    _timer.setTime(seconds, _player.duration())
    if (seconds <= 0) {
      clearInterval(interval)
    }
  }
  calcTimer()
  interval = setInterval(() => {
    calcTimer()
  }, 500)
}

function setUpNavigationManager () {
  let navigationManager = new NavigationManager(undefined, adTypes.VIDEO, subscribe)
  CommunicationManager.setNavigationManager(navigationManager)
}

function subscribe (event) {
  requestUrl(_ad.clickTracker)
  CommunicationManager.send({
    action: senderEvents.ACTION_INVOKED,
    advertisement: _ad
  }, event.senderId)
}

export function closeVideo (shouldRunCallback) {
  'use strict'
  _player.dispose()
  _player = undefined
  if (_timer) {
    _timer.remove()
  }
  if (isFunction(_onAdEnd) && shouldRunCallback) {
    _onAdEnd()
  } else {
    closeAd()
  }
}



