import { closeAd, showThankYouScreen } from '../ColorTVSDK'
import { neighbours } from './../constants'
import { isFunction } from '../Other/helpers'
let _currentElement, _neighborhood, _adType, _onSubscribe
let _listeners = []

class NavigationManager {
  constructor (startingNode, adType, subscribe) {
    this._setCurrentElement(startingNode)
    _adType = adType
    _neighborhood = neighbours[ adType ]
    _onSubscribe = subscribe
  }

  /*
   Register listener function for listening on navigation change
   */
  registerListener (func) {
    if (isFunction(func) && _listeners.indexOf(func) === -1) {
      _listeners.push(func)
    }
  }

  removeListener (func) {
    let index = _listeners.indexOf(func)
    if (index > -1) {
      _listeners.splice(index, 1)
      return true
    }
    return false
  }

  clearListeners () {
    _listeners = []
  }

  debug () {
    console.log({
      _currentElement: _currentElement,
      _neighborhood: _neighborhood,
      _adType: _adType
    })
  }

  _setCurrentElement (node) {
    if (_currentElement) {
      this._removeFocus(_currentElement)
    }
    _currentElement = node
    if (_currentElement) {
      this._setFocus(_currentElement)
    }
  }

  _setFocus (node) {
    node.classList.add('focused')
  }

  _removeFocus (node) {
    node.classList.remove('focused')
  }

  up (event) {
    this._performChange('up')
  }

  down (event) {
    this._performChange('down')
  }

  left (event) {
    this._performChange('left')
  }

  right (event) {
    this._performChange('right')
  }

  center (event) {
    if (_currentElement) {
      _currentElement.click()
      this._callListenerFunction('center', event)
    }
  }

  otherAction (event) {
    this._callListenerFunction('other', event)
  }

  subscribe (event) {
    showThankYouScreen(event)
    if (isFunction(_onSubscribe)) {
      _onSubscribe(event)
      this._callListenerFunction('subscribe', event)
    }
  }

  close (event) {
    closeAd()
    this._callListenerFunction('close', event)
  }

  _performChange (direction) {
    if (_currentElement) {
      let nextElement = NavigationManager._getNextElement(_currentElement, direction) || []
      nextElement.length !== 0 ? this._setCurrentElement(nextElement) : false
      this._callListenerFunction(direction)
    }
  }

  performManualChange (nextElement, direction) {
    if (_currentElement) {
      nextElement.length !== 0 ? this._setCurrentElement(nextElement) : false
      this._callListenerFunction(direction)
    }
  }

  _callListenerFunction (direction) {
    _listeners.forEach((func) => {
      if (isFunction(func)) {
        func(direction)
      }
    })
  }

  static _getNextElement (currentElement, direction) {
    let id = currentElement.id
    let nextElement = document.querySelector(`#${_neighborhood[ id ][ direction ]}`)
    return nextElement
  }

  getCurrentElement () {
    return {
      node: _currentElement,
      id: _currentElement.id
    }
  }
}

export default NavigationManager



