import SenderManager from './SenderManager'
import { debug, sdkVersion } from './../constants'
import { getTimeZone } from '../Other/EnviormentDetails'
import { config, configParams } from './ConfigManager'
import * as WebManager from './WebManager'
let _instance
let SESSION_RETRY_TIMEOUT = 5000
let PING_TIMEOUT = 4000
class SessionManager {

  constructor () {
    this.setRequestInProgress(false)
  }

  requestSession () {
    if (this.isRequestInProgress()) {
      this.setRequestAwaiting(true)
      return
    }
    // if (debug) {
    //   console.groupCollapsed('%c REQUEST SESSION ', 'color:#FFD700;background-color:#333333;padding:5px 10px;')
    //   console.log('Gathered Data: ')
    //   console.log(this.gatherDataForRequest())
    //   console.groupEnd()
    // }
    this.clearPingTimeout()
    this.setRequestInProgress(true)
    this.makeActualSessionRequest(this.gatherDataForRequest()).then((responseFromServer) => {
      this.handleSessionRequestSuccess(responseFromServer)
    }).catch((responseFromServer) => {
      this.handleSessionRequestFail(responseFromServer)
    })
  }

  makeActualSessionRequest (dataForServer) {
    return new Promise((resolve, reject)=> {
      WebManager.postSession('POST', dataForServer).then((responseFromServer) => {
        resolve(responseFromServer)
      }).catch((responseFromServer) => {
        reject(responseFromServer)
      })
    })
  }

  handleSessionRequestSuccess (responseFromServer) {
    this.setSessionObject(responseFromServer)
    this.setRequestInProgress(false)
    SenderManager.broadCastSessionInfo(this.getSessionObject())
    if (this.isRequestAwaiting()) {
      this.setRequestAwaiting(false)
      this.requestSession()
    } else {
      if (responseFromServer.pingInterval) {
        this.pingIntervalFromServer = responseFromServer.pingInterval
      }
      this.setupPinging()
    }
  }

  setSessionObject (response) {
    this.sessionObject = response
  }

  getSessionObject () {
    return this.sessionObject
  }

  isRequestAwaiting () {
    return this.nextRequestAwait
  }

  setRequestAwaiting (value) {
    this.nextRequestAwait = value
  }

  isRequestInProgress () {
    return this.requestInProgress
  }

  setRequestInProgress (value) {
    this.requestInProgress = value
  }

  clearPingTimeout () {
    if (this.pingTimeout) {
      clearTimeout(this.pingTimeout)
    }
  }

  setupPinging () {
    this.pingTimeout = setTimeout(() => {
      this.ping()
    }, this.pingIntervalFromServer * 100 || PING_TIMEOUT)
  }

  ping () {
    this.setRequestInProgress(true)
    this.makePingRequest().then((responseFromServer) => {
      this.setSessionObject(responseFromServer)
      SenderManager.broadCastSessionInfo(this.getSessionObject())
      this.setRequestInProgress(false)
      if (this.isRequestAwaiting()) {
        this.clearPingTimeout()
        this.setRequestAwaiting(false)
        this.requestSession()
      } else {
        this.setRequestAwaiting(false)
        this.setupPinging()
      }
    }).catch(() => {
      this.setRequestInProgress(false)
    })
  }

  makePingRequest () {
    return new Promise((resolve, reject)=> {
      WebManager.postSession('PUT', this.gatherDataForRequest())
        .then((responseFromServer) => {
          resolve(responseFromServer)
        }).catch((responseFromServer) => {
        reject(responseFromServer)
      })
    })
  }

  handleSessionRequestFail () {
    if (debug) {
      console.error('%c SESSION FAILED', 'color:black;background-color:red;padding:5px 10px;')
    }
    this.setRequestInProgress(false)
    this.setRequestAwaiting(false)
    setTimeout(() => {
      this.requestSession()
    }, SESSION_RETRY_TIMEOUT)
  }

  gatherDataForRequest () {
    return {
      sessionId: this.getSessionObject() ? this.getSessionObject().sessionId : void 0,
      identifierForAds: '',
      sdkVersion: sdkVersion,
      appVersion: config[ configParams.APP_VERSION ],
      timezone: getTimeZone(),
      devices: SenderManager.getSendersInfo()
    }
  }

  stopSession () {
    WebManager.postSession('DELETE')
  }
}

function getInstance () {
  'use strict';
  if (!_instance) {
    _instance = new SessionManager()
  }
  return _instance
}

export default getInstance()


