let timerHTML = require('html!./timer.html')
class Timer {

  constructor (node, duration, callbackOnEnd) {
    this.node = node
    this.completeDuration = Math.ceil(duration) - 1
    this.seconds = 0
    this.unit = 180 / this.completeDuration
    this.progress = 0
    this.callbackOnEnd = callbackOnEnd
  }

  start () {
    if (!this.node) {
      throw new Error('No Timer Node is present')
    }
    this.node.style.display = 'initial'
    this.compute()
    this.interval = setInterval(() => {
      this.seconds++
      this.compute()
    }, 1000)
  }

  _set (degrees, time) {
    this.node.querySelector('.numbers').innerHTML = time
    this.node.querySelector('.circle .mask.full').style.transform = `rotate(${degrees}deg)`
    this.node.querySelectorAll('.circle .fill').forEach((elem) => {
      elem.style.transform = `rotate(${degrees}deg)`
    })
  }

  compute () {
    this.value = this.completeDuration - this.seconds
    this.progress = this.seconds * this.unit
    this.degrees = 180 - this.progress
    this._set(this.degrees, this.value)
    if (this.value <= 0) {
      clearInterval(this.interval)
      setTimeout(() => {
        this.node.style.display = 'none'
        if (this.callbackOnEnd && typeof (this.callbackOnEnd) === 'function') {
          this.callbackOnEnd()
        }
      }, 1000)
    }
  }

  setTime (seconds) {
    if (!this.node) {
      throw new Error('No Timer Node is present')
    }
    this.node.style.display = 'initial'
    this._set(Math.min((seconds * (180 / this.completeDuration)), 180), seconds)
  }

  printDebugInfo () {
    console.log('Value', this.value)
    console.log('Progress', this.progress)
    console.log('Complete Duration', this.completeDuration)
    console.log('Seconds', this.seconds)
    console.log('Unit', this.unit)
    console.log('Degrees', this.degrees)
  }

  static getHtml () {
    let div = document.createElement('div')
    div.innerHTML = timerHTML
    return div
  }

  remove () {
    clearInterval(this.interval)
    this.node.remove()
  }
}

export default Timer



