/* global XMLHttpRequest */
import { AdServerAddress, HttpRequestTimeout, errorMessages, debug, responseTypeFromServer } from './../constants'
import { config, configParams } from './ConfigManager'
// https://adsrv-stage.colortv.com/ad/req?app=d48eec15-d2ba-41c9-8829-f2860b175668&placement=TestFullScreen
export function getAd (placement, customUrlParams, adType) {
  'use strict'
  let data = {
    app: config.appId,
    placement: placement,
    type: adType,
    sessionId: config[ configParams.SESSION_ID ]
  }
  /*
   This code merge two objects: customUrlParams and data
   */
  if (customUrlParams) {
    Object.keys(customUrlParams).forEach((key) => {
      data[ key ] = customUrlParams[ key ]
    })
  }
  return new Promise((resolve, reject) => {
    let ajax = new XMLHttpRequest()
    ajax.responseType = 'json'
    ajax.timeout = HttpRequestTimeout
    ajax.onloadend = (resp) => {
      let response = resp.target.response
      if (debug) {
        console.groupCollapsed('Response from server')
        console.log('Response:', response)
        console.groupEnd()
      }
      if (response.error) {
        switch (response.error) {
          case responseTypeFromServer.NO_ADS:
            let err = errorMessages[ 13 ]
            err.reason = response.error
            reject(err)
            break
          default:
            reject(response)
            break
        }

      } else {
        resolve(response)
      }
    }
    ajax.onerror = (error) => {
      let err = errorMessages[ 6 ]
      err.reason = error
      reject(err)
    }
    ajax.onabort = (error) => {
      let err = errorMessages[ 7 ]
      err.reason = error
      reject(err)
    }
    ajax.ontimeout = (error) => {
      let err = errorMessages[ 8 ]
      err.reason = error
      reject(err)
    }
    ajax.open('GET', `${AdServerAddress}/ad/req?${_parseObjectToGETparams(data)}`, true)
    ajax.send()
  })
}

function _parseObjectToGETparams (data) {
  'use strict'
  let result = ''
  for (let key in data) {
    result += `${key}=${data[ key ]}&`
  }
  return result
}

export function postSession (method, bodyData) {
  return new Promise((resolve, reject) => {
    let ajax = new XMLHttpRequest()
    ajax.open(method, `${AdServerAddress}/app/sess/${config.appId}`)
    ajax.setRequestHeader('Content-type', 'application/json')
    ajax.responseType = 'json'
    ajax.timeout = HttpRequestTimeout
    ajax.onloadend = (resp) => {
      let data = resp.target.response
      let status = resp.target.status
      if (status && (status >= 200 && status < 300 || status === 304)) {
        resolve(data)
      } else {
        reject({
          errorCode: 404
        })
      }
    }
    ajax.onerror = () => {
      reject({
        errorCode: 404
      })
      throw new Error('Error during requesting session')
    }
    ajax.onabort = (error) => {
      reject({
        errorCode: 404
      })
      throw new Error('Error during posting session => abort: ', error)
    }
    ajax.ontimeout = (error) => {
      reject({
        errorCode: 404
      })
      throw new Error('Error during posting session => timeout: ', error)
    }
    ajax.send(_prepareRequestBody(bodyData))
  })
}

function _prepareRequestBody (bodyData) {
  let requestBody = {
    sessionId: config[ configParams.SESSION_ID ],
    timezone: Intl.DateTimeFormat().resolvedOptions().timeZone,
    language: navigator.language,
    thirdPartyUserId: config[ configParams.USER_ID ],
    sdkVersion: config[ configParams.SDK_VERSION ]
  }
  if (bodyData) {
    requestBody = Object.assign(requestBody, bodyData)
  }
  return JSON.stringify(requestBody)
}

export function requestUrl (url) {
  return new Promise((resolve, reject) => {
    let ajax = new XMLHttpRequest()
    ajax.open('GET', url, true)
    /*
     If there is no response for 5 seconds abort request
     */
    ajax.timeout = HttpRequestTimeout
    ajax.onload = (resp) => {
      let { status } = resp.target
      if (status >= 200 && status < 300 || status === 304) {
        resolve(resp.target.response)
      } else {
        reject(resp.target.response)
        throw new Error(resp.target.response)
      }
    }
    ajax.onerror = (error) => {
      reject(error)
      throw new Error(`Error during requesting url ${url},`, error)
    }
    ajax.onabort = (error) => {
      reject(error)
      throw new Error(`Error during requesting url ${url} => abort: `, error)
    }
    ajax.ontimeout = (error) => {
      reject(error)
      throw new Error(`Error during requesting url ${url} => timeout: `, error)
    }
    ajax.send()
  })
}



