/* global Image */
import { showEngagementScreen } from './AdUnits/Engagement'
import { showInterstitialScreen, closeInterstitial } from './AdUnits/Interstitial'
import { showDiscoveryScreen } from './AdUnits/Discovery'
import { showContentScreen } from './AdUnits/Content'
import { getAd } from './Singleton_Managers/WebManager'
import {
  adTypes,
  placements,
  senderEvents,
  sdkVersion,
  errorMessages,
  innerSDKEvents
} from './constants'
import { showVideo, closeVideo } from './AdUnits/Video'
import SessionManager  from './Singleton_Managers/SessionManager'
import { config, configParams } from './Singleton_Managers/ConfigManager'
import { fancySDKoutput } from './Other/onStartup'
import Timer from '../components/timer/timer'
import DeveloperCallbackManager from './Singleton_Managers/DeveloperCallbackManager'
import { isFunction } from './Other/helpers'
import * as PollingUrlManager from './Singleton_Managers/PollingUrlManager'
import CommunicationManager from './Singleton_Managers/CommunicationManager'
import  RecentAds  from './Singleton_Managers/RecentAds'
import SenderManager from './Singleton_Managers/SenderManager'   // eslint-disable-line

let _overlay = require('html!./../html/overlay.html')
let _node, _showAdEnd
export let SDKInstance
export let openedAdResponse, currentlyDisplayed
/**
 *
 * @param params
 * Can take :
 * APP_ID // MUST HAVE
 * appVersion // OPTIONAL
 * @param callback
 * @returns {Promise}
 */
export function init (params = {}, callback) {
  return new Promise((resolve, reject) => {
    if (!params.appId) {
      throwError(errorMessages[ 5 ], reject, callback)
    }
    _prepareConfig(params)
    PollingUrlManager.requestPollingUrls()
    DeveloperCallbackManager.onSdkInitialized()
    fancySDKoutput()
    if (isFunction(callback)) {
      callback()
    }
    resolve()
  })
}

export function setCurrentlyDisplayed (adType) {
  currentlyDisplayed = adType
}

function _prepareConfig (params = {}) {
  config[ configParams.APP_ID ] = params.appId
  config[ configParams.APP_VERSION ] = params.appVersion
  config[ configParams.SDK_VERSION ] = sdkVersion
}

/*
 Params:
 * placement
 * customUrlParams
 * customFeatureAdUrlParams
 */
export function loadAd (parameters = {}, callback) {
  let { placement, customUrlParams, customFeatureAdUrlParams } = parameters
  return new Promise((resolve, reject) => {
    if (!placement) {
      throwError(errorMessages[ 3 ], reject, callback)
      return
    }
    if (!checkIfPlacementIsValid(placement)) {
      throwError(errorMessages[ 14 ], reject, callback)
      return
    }
    if (!config) {
      throwError(errorMessages[ 4 ], reject, callback)
      return
    }
    getAd(placement, customUrlParams).then((response) => {
      if (response.type === adTypes.DISCOVERY_CENTER || response.type === adTypes.CONTENT_RECOMMENDATION) {
        _loadFeaturedAd(placement, customFeatureAdUrlParams).then((featureAdResponse) => {
          if (featureAdResponse) {
            response.featuredAd = featureAdResponse
            _onAdLoaded(placement, response, resolve, callback)
          } else {
            throwError(errorMessages[ 9 ], reject, callback)
          }
        }).catch((error) => {
          let err = errorMessages[ 10 ]
          err.reason = error
          throwError(err, reject, callback)
        })
      } else {
        _onAdLoaded(placement, response, resolve, callback)
      }
    }).catch((error) => {
      throwError(error, reject, callback)
    })
  })
}

function throwError (error, reject, callback) {
  if (isFunction(callback)) {
    callback(error)
  }
  reject(error)
}
function checkIfPlacementIsValid (placement) {
  let isValid = false
  for (let key in placements) {
    if (placements[ key ] === placement) {
      isValid = true
    }
  }
  return isValid
}
function _loadFeaturedAd (placement, customFeatureAdUrlParams = {}) {
  return new Promise((resolve, reject) => {
    getAd(placement, customFeatureAdUrlParams, adTypes.FEATURED).then((response) => {
      resolve(response)
    }).catch((error) => {
      reject(error)
    })
  })
}

function _onAdLoaded (placement, response, resolve, callback) {
  _saveAd(placement, response)
  preloadAssets(response).then(() => {
    DeveloperCallbackManager.onAdLoaded()
    if (isFunction(callback)) {
      callback()
    }
    resolve()
  })
}

function _saveAd (placement, response) {
  RecentAds.setAdResponse(placement, response)
}

export function preloadAssets (response) {
  return new Promise((resolve, reject) => {
    preloadImages(response).then(() => {
      resolve()
    })
  })
}

function preloadImages (response) {
  return new Promise((resolve, reject) => {
    let tmp
    let promArray = []
    switch (response.type) {
      case adTypes.ENGAGEMENT:
        promArray.push(new Promise((resolve, reject) => {
          tmp = new Image()
          tmp.onload = () => {
            resolve()
          }
          tmp.onerror = () => {
            reject()
          }
          tmp.src = response.ads[ 0 ].backgroundUrl
        }))
        if (response.ads[ 0 ].postEngagementUrl) {
          promArray.push(new Promise((resolve, reject) => {
            tmp = new Image()
            tmp.onload = () => {
              resolve()
            }
            tmp.onerror = () => {
              reject()
            }
            tmp.src = response.ads[ 0 ].postEngagementUrl
          }))
        }
        Promise.all(promArray).then(() => {
          resolve()
        })
        break
      case adTypes.INTERSTITIAL:
        response.ads[ 0 ].screenshots.forEach((image) => {
          promArray.push(new Promise((resolve, reject) => {
            tmp = new Image()
            tmp.onload = () => {
              resolve()
            }
            tmp.onerror = () => {
              reject()
            }
            tmp.src = image
          }))
        })
        Promise.all(promArray).then(() => {
          resolve()
        })
        break
      case adTypes.DISCOVERY_CENTER:
      case adTypes.CONTENT_RECOMMENDATION:
        response.ads.forEach((ad) => {
          // Preload images for items in grid
          promArray.push(new Promise((resolve, reject) => {
            let tmp = new Image()
            tmp.onload = () => {
              resolve()
            }
            tmp.onerror = () => {
              reject()
            }
            tmp.src = ad.backgroundUrl
          }))
          // Preload feature ad
          promArray.push(new Promise((resolve, reject) => {
            let tmp = new Image()
            tmp.onload = () => {
              resolve()
            }
            tmp.onerror = () => {
              reject()
            }
            tmp.src = response.featuredAd.ads[ 0 ].backgroundUrl
          }))
        })
        Promise.all(promArray).then(() => {
          resolve()
        })
        break
      default:
        resolve()
        break
    }
  })
}

export function showAd (parameters = {}, callback) {
  return new Promise((resolve, reject) => {
    let { placement, node } = parameters

    _showAdEnd = {
      promiseResolve: resolve
    }
    if (!placement) {
      throwError(errorMessages[ 1 ], reject, callback)
      return
    }
    if (!node) {
      throwError(errorMessages[ 2 ], reject, callback)
      return
    }
    if (node.jquery) {
      node = node[ 0 ]
      console.warn("ColorTv SDK: Detected jQuery object passed to showAd. It's advised to pass pure Vanilla JS DOM object.")
    }
    openedAdResponse = RecentAds.getAdResponse(placement)
    if (!(openedAdResponse instanceof Object)) {
      _handleShowError(openedAdResponse, placement, reject, callback)
    }
    try {
      _node = node
      _node.style.display = 'initial'
      _node.innerHTML = _overlay
    } catch (error) {
      throwError(errorMessages[ 15 ], reject, callback)
      return
    }
    if (isFunction(callback)) {
      _showAdEnd.callback = callback
    }
    let nodeForChildren = document.querySelector('#colorTvOverlay .content')
    /*
     *  Discovery and content recommendation calls polling urls themselves
     */
    // TODO : replace this placements before release
    if (placement !== placements.APP_WALL && placement !== placements.CONTENT) {
      PollingUrlManager.registerPollingUrl(openedAdResponse)
    }
    _openAd(openedAdResponse, nodeForChildren)
  })
}

function _openAd (adResponse, node) {
  switch (adResponse.type) {
    case adTypes.ENGAGEMENT:
      showSingleAd(node, adResponse, showEngagementScreen)
      break
    case adTypes.VIDEO:
      showVideo(node, adResponse)
      break
    case adTypes.INTERSTITIAL:
      showSingleAd(node, adResponse, showInterstitialScreen)
      break
    case adTypes.DISCOVERY_CENTER:
      showSingleAd(node, adResponse, showDiscoveryScreen)
      break
    case adTypes.CONTENT_RECOMMENDATION:
      showSingleAd(node, adResponse, showContentScreen)
      break
    default:
      DeveloperCallbackManager.onUnsupportedAdType()
      throw new Error('Unsupported ad type')
  }
}

export function showThankYouScreen (event) {
  // TODO: when user have control during displaying this screen he can break navigation pretty badly. Fix ASAP
  if (openedAdResponse.type === adTypes.ENGAGEMENT) {
    document.querySelector('#colorTvOverlay .content').innerHTML = `
             <div class="thankYouScreen">
             </div>`
    document.querySelector('#colorTvOverlay .content .thankYouScreen').style.backgroundImage = `url(${openedAdResponse.ads[ 0 ].postEngagementUrl})`
  }
}

function showSingleAd (node, adResponse, showAd) {
  document.querySelector('#colorTvOverlay').appendChild(Timer.getHtml())
  let timer
  let closeDuration = adResponse.autoCloseDuration || adResponse.featuredAd.autoCloseDuration
  if (adResponse.autoCloseVisible === 'true' || adResponse.featuredAd.autoCloseVisible === 'true') {
    timer = new Timer(document.querySelector('#colorTvOverlay .radial-progress'), closeDuration, function () {
      onBackButton()
    })
  }
  let didUserMovedDuringVideo = false
  let didUserSubscribedOnEngagement = false
  if (adResponse.ads[ 0 ].markupUrl) {
    showVideo(node, adResponse, () => {
      (!didUserMovedDuringVideo && timer) ? timer.start() : false
      if (!didUserSubscribedOnEngagement) {
        showAd({
          node,
          adResponse,
          dontRunAdStarted: true
        })
      }
    })
    // TODO : think of better way to do this
    CommunicationManager.getNavigationManager().registerListener((direction) => {
      didUserMovedDuringVideo = true
      timer ? timer.remove() : false
      if (direction === 'subscribe' && openedAdResponse.type === adTypes.ENGAGEMENT) {
        didUserSubscribedOnEngagement = true
      }
    })
  } else {
    (!didUserMovedDuringVideo && timer) ? timer.start() : false
    showAd({
      node, adResponse
    })
    // TODO : same code as above
    CommunicationManager.getNavigationManager().registerListener(() => {
      didUserMovedDuringVideo = true
      timer ? timer.remove() : false
    })
  }
}

function _handleShowError (response, placement, reject, callback) {
  if (typeof response !== 'string') {
    return
  }
  if (response === innerSDKEvents.AD_NOT_LOADED) {
    _onAdNotLoaded(placement, reject, callback)
  } else if (response === innerSDKEvents.AD_EXPIRED) {
    _onAdExpired(placement, reject, callback)
  }

}

function _onAdNotLoaded (placement, reject, callback) {
  DeveloperCallbackManager.onAdNotLoaded()
  throwError(errorMessages[ 11 ](placement), reject, callback)
}

function _onAdExpired (placement, reject, callback) {
  DeveloperCallbackManager.onAdExpired(placement)
  throwError(errorMessages[ 12 ](placement), reject, callback)
}
export function onBackButton () {
  switch (currentlyDisplayed) {
    case adTypes.VIDEO:
      closeVideo(false)
      break
    case adTypes.INTERSTITIAL:
      closeInterstitial()
      closeAd()
      break
    default:
      closeAd()
      break
  }
}

export function closeAd () {
  PollingUrlManager.requestPollingUrls()
  if (!_node) {
    return
  }
  _node.innerHtml = ''
  _node.style.display = 'none'
  _node = undefined
  currentlyDisplayed = undefined
  openedAdResponse = undefined
  CommunicationManager.getNavigationManager().clearListeners()
  CommunicationManager.removeNavigationManager()
  CommunicationManager.send({
    action: senderEvents.AD_CLOSED
  })
  DeveloperCallbackManager.onAdClosed()
  if (_showAdEnd) {
    _showAdEnd.promiseResolve()
    if (isFunction(_showAdEnd.callback)) {
      _showAdEnd.callback()
    }
    _showAdEnd = undefined
  } else {
    throw new Error('Show ad end object is missing!')
  }
}

export function isAdOpened () {
  return _node !== undefined
}

export function onSenderConnected (event) {
  CommunicationManager.onSenderConnected(event)
}

export function onSenderDisconnected (event) {
  CommunicationManager.onSenderDisconnected(event)
}

export function onSystemVolumeChanged (event) {
  CommunicationManager.onSystemVolumeChanged(event)
}

export function onShutdown (event) {
  CommunicationManager.onShutdown(event)
  SessionManager.stopSession()
}

export function setUserId (userId) {
  config[ configParams.USER_ID ] = userId
}



