/*
 This manager is responsible for storing fetched ad responses for specific placement
 When we receive adREsponse we save it to specific placement.
 Then we can retrive them by getAdResponse but before that we check if ad is still valid
 ( for example we can check if ad expired and react on that)
 */
import { innerSDKEvents } from './../constants'
let _instance
let _adResponses

class RecentAds {
  constructor () {
    _adResponses = {}
  }

  setAdResponse (placement, adResponse) {
    if (!this.isAdResponseValid(placement)) {
      _adResponses[ placement ] = adResponse
    }
  }

  getAdResponse (placement) {
    let adResponse = _adResponses[ placement ]
    if (!adResponse) {
      return innerSDKEvents.AD_NOT_LOADED
    } else if (this._hasExpired(adResponse)) {
      this._removeAdResponse(placement)
      return innerSDKEvents.AD_EXPIRED
    } else {
      return adResponse
    }
  }

  // TODO : ask Mikołaj if this 'if' is valid
  isAdResponseValid (placement) {
    let adResponse = this.getAdResponse(placement)
    if (adResponse) {
      return false
    } else {
      return !this._hasExpired(adResponse)
    }
  }

  _hasExpired (adResponse) {
    let currentDate = new Date()
    let expirationDate = new Date(adResponse.validUntil)
    return currentDate > expirationDate
  }

  _removeAdResponse (placement) {
    delete _adResponses[ placement ]
  }
}

function getInstance () {
  'use strict';
  if (!_instance) {
    _instance = new RecentAds()
  }
  return _instance
}

export default getInstance()



